@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-screen{color:#fff;background-color:#010606;box-sizing:border-box;margin:0;padding:20px 0;height:100vh;font-family:"Roboto", sans-serif;display:flex;flex-direction:column;justify-content:center;align-items:center}@media screen and (max-width: 350px){.main-screen .title{font-size:24px}}

.vp-container{width:80%;min-width:200px;max-width:1200px;padding:10px;border:1px solid #303030;box-shadow:5px 2px 2px #303030}.vp-container .video{position:relative;z-index:1}.vp-container .video .image{position:absolute;top:50%;left:50%;background:rgba(48,48,48,0.5);border-radius:50%;display:none}.vp-container .video .image2{position:absolute;top:50%;left:50%;background:rgba(48,48,48,0.5);border-radius:50%;display:block;animation:slidein 1s ease-in-out}@keyframes slidein{0%{transform:scale(0)}100%{transform:scale(1)}}.vp-container .video .timestamp{font-size:12px;position:absolute;left:5px;bottom:1px;background-color:black;padding:5px;opacity:0.8}.vp-container .video .speed{position:absolute;right:0;bottom:0;background-color:black;opacity:0.8;padding:5px}.vp-container .video .speed .speed-title{border-bottom:1px solid #303030}.vp-container .video .speed div{font-size:12px;padding:5px}.vp-container .video .speed div:hover{background-color:#303030}.vp-container .progress-bar div input[type="range" i]{width:100%;overflow:hidden;height:3px;cursor:pointer}.vp-container .progress-bar div input[type="range" i]:hover{height:4px}.vp-container .progress-bar div input[type="range"]::-webkit-slider-thumb{width:10px}.vp-container .progress-bar div input[type="range"]::-moz-range-thumb{width:10px}.vp-container .progress-bar div input[type="range"]::-ms-thumb{width:10px}.vp-container .video-controls{display:flex;justify-content:space-between;padding-top:5px}.vp-container .video-controls .controls-left,.vp-container .video-controls .controls-right{display:flex;align-items:center;cursor:pointer;margin:0}.vp-container .video-controls .controls-left div,.vp-container .video-controls .controls-right div{padding-right:5px}.vp-container .video-controls .controls-left .volume:hover+.volumeProgress,.vp-container .video-controls .controls-right .volume:hover+.volumeProgress{visibility:visible}.vp-container .video-controls .controls-left .volumeProgress,.vp-container .video-controls .controls-right .volumeProgress{width:30px;visibility:hidden}.vp-container .video-controls .controls-left .volumeProgress:hover,.vp-container .video-controls .controls-right .volumeProgress:hover{visibility:visible}.vp-container .video-controls .controls-left .volumeProgress input[type="range" i],.vp-container .video-controls .controls-right .volumeProgress input[type="range" i]{width:50px;margin-bottom:50%;height:3px}@media screen and (max-width: 350px){.vp-container .video .speed div{font-size:8px}}@media screen and (min-width: 900px) and (max-height: 600px){.vp-container{width:50%}}@media screen and (min-width: 1500px) and (max-height: 800px){.vp-container{width:40%}}

